/* General header styling */
header {
    background-color: #203166; /* Ensures full header background color */
    padding: 10px 0; /* Adds some padding for better spacing */
  }
  
  /* Navigation links styling */
  .nav-links {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-around; /* Ensures even spacing */
    align-items: center; /* Centers items vertically */
  }
  
  .nav-links li {
    position: relative;
  }
  
  a:link, a:visited {
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
    display: inline-block;
  }

  /* Dropdown styling */
  .dropdown {
    position: relative;
  }
  
  .dropdown span {
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
    display: inline-block;
    cursor: pointer;
  }
  
  .dropdown-menu {
    display: none;
    position: absolute;
    top: 40px; /* Adjusted to ensure dropdown is below the header */
    left: 0;
    background-color: #203166; /* Matching the header background color */
    border: 1px solid #ccc;
    padding: 0;
    list-style: none;
    min-width: 150px; /* Ensures dropdown has a minimum width */
    z-index: 1;
  }
  
  .dropdown:hover .dropdown-menu,
  .dropdown-menu:hover {
    display: block;
  }
  
  .dropdown-menu li {
    margin: 0;
  }
  
  .dropdown-menu li a {
    text-decoration: none;
    color: white;
    padding: 10px 20px;
    display: block;
  }
  
  .dropdown-menu li a:hover {
    background-color: #17234b; /* Slightly darker background on hover */
  }
  